.besty-builds-page {
  background: var(--bg);
  color: var(--text);
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  overflow-x: hidden;
}

/* Hero Section */
.hero-bestybuilds {
  position: relative;
  min-height: 70vh;
  display: grid;
  place-items: center;
  padding: 100px 20px;
  background: radial-gradient(1000px 600px at 10% -20%, rgba(39, 174, 96, 0.15), transparent 60%),
              radial-gradient(900px 600px at 90% 120%, rgba(167, 243, 208, 0.12), transparent 60%),
              linear-gradient(180deg, #0e1c16 0%, #0b1511 100%);
  border-radius: 0 0 20px 20px;
}

.hero-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  z-index: 1;
}

.hero-inner {
  position: relative;
  z-index: 2;
  max-width: 900px;
  text-align: center;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 800;
  margin-bottom: 1rem;
  color: #fff;
}

.hero-tagline {
  font-size: 1.8rem;
  color: var(--accent-2);
  font-weight: 600;
}

.hero-sub {
  color: var(--muted);
  max-width: 740px;
  margin: 0 auto 2rem;
  font-size: 1.1rem;
  line-height: 1.6;
}

.hero-cta {
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
  margin-top: 2rem;
}

/* Sections */
.section {
  padding: 80px 20px;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.section-title {
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 3rem;
  color: #fff;
  font-weight: 700;
}

.section-text {
  max-width: 820px;
  margin: 1.5rem auto;
  color: var(--muted);
  font-size: 1.15rem;
  line-height: 1.8;
  text-align: justify;
}

/* Cards */
.card {
  background: rgba(17, 50, 36, 0.9);
  border: 1px solid rgba(123, 211, 137, 0.15);
  border-radius: 16px;
  padding: 1.5rem;
  transition: transform 220ms ease;
}

.card:hover {
  transform: translateY(-6px);
}

.project-card {
  display: flex;
  flex-direction: column;
}

.video-frame {
  position: relative;
  width: 100%;
  padding-top: 56.25%;
  border-radius: 12px;
  overflow: hidden;
  background: #000;
  margin-bottom: 1rem;
}

.video-frame iframe {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.card-title {
  margin-bottom: 0.75rem;
  color: #fff;
  font-size: 1.25rem;
  font-weight: 600;
}

.card-desc {
  color: var(--muted);
  line-height: 1.6;
  flex-grow: 1;
}

/* Category Cards */
.category-card {
  display: flex;
  justify-content: space-between;
  text-decoration: none;
  color: var(--text);
  padding: 1.5rem;
  border-radius: 16px;
  background: rgba(17, 50, 36, 0.85);
  border: 1px solid rgba(123, 211, 137, 0.18);
  transition: transform 220ms ease;
}

.category-card:hover {
  transform: translateY(-3px);
}

.category-title {
  font-weight: 700;
  font-size: 1.1rem;
}

.category-arrow {
  color: var(--accent-2);
  font-size: 1.4rem;
  font-weight: bold;
}

/* Stats Grid */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin: 2rem 0;
}

.stat-card {
  background: rgba(17, 50, 36, 0.8);
  border-radius: 16px;
  padding: 2rem;
  text-align: center;
  transition: transform 220ms ease;
}

.stat-card:hover {
  transform: translateY(-4px);
}

.stat-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.stat-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: #fff;
}

.stat-desc {
  color: var(--muted);
  font-size: 1rem;
  line-height: 1.5;
}

/* USP Section */
.usp {
  margin-top: 3rem;
  display: grid;
  gap: 1.5rem;
}

.usp-item {
  display: flex;
  gap: 1rem;
  background: rgba(17, 50, 36, 0.6);
  border-radius: 14px;
  padding: 1.5rem;
}

.usp-icon {
  font-size: 2rem;
}

.usp-title {
  color: #fff;
  font-size: 1.2rem;
  font-weight: 600;
}

.usp-desc {
  color: var(--muted);
  line-height: 1.5;
}

/* CTA Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 1rem 1.5rem;
  border-radius: 12px;
  font-weight: 700;
  transition: transform 180ms ease;
}

.btn:active {
  transform: translateY(1px);
}

.btn-yt {
  background: var(--yt);
  color: #fff;
}

.btn-yt:hover {
  background: #e0002e;
  transform: translateY(-2px);
}

.btn-ghost {
  background: transparent;
  color: var(--accent-2);
  border: 1px solid rgba(167, 243, 208, 0.35);
}

.btn-outline {
  background: transparent;
  color: #fff;
  border: 1px solid rgba(123, 211, 137, 0.45);
}

/* Responsive */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
  }

  .hero-tagline {
    font-size: 1.4rem;
  }

  .section {
    padding: 60px 20px;
  }

  .btn {
    width: 100%;
    max-width: 250px;
  }

  .usp {
    grid-template-columns: 1fr;
  }
}

@media (min-width: 1024px) {
  .usp {
    grid-template-columns: repeat(3, 1fr);
  }
}
